<template>
    <div class="gw-0">
        <div class="gw-1" ref="gw" style="background: white;">
            <div v-for="item ,index in info" class="gw-2" :key="index" :name="item">
                <div class="gw-3shang">
                    <img v-lazy="item.spare" height="219px" width="238px" alt="商品">
                </div>
                <div class="gw-3zhong">
                    <img src="https://a.appsimg.com/upload/brandcool/0/3102fab5d5174dea8ea95cf36b7a09df/10010474/primary.png!75.webp" height="40px" width="80px" alt="品牌">
                </div>
                <div class="gw-3xia">
                    <div style="display: flex;width: 200px;flex-direction: column;margin-top: 45px;justify-content: space-evenly;">
                        <div style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{item.goods}}</div>
                        <div>0.8折{{item.price}}</div>
                        <el-tooltip class="item" effect="dark" content="加入购物车" placement="top" style="font-size:16px">
                            <el-button type="danger" icon="el-icon-plus"  @click="gm" >添加</el-button>
                        </el-tooltip>
                        
                        
                    </div>
                    
                </div>
            </div>
            <div >
                            <div v-show="show" id="cricle" >1</div>
                        </div>
            <el-button type="danger" style="width: 129px;position: absolute;left: 50%;bottom: -5px;transform: translateX(-50%);" @click="shang">点击加载更多</el-button>
        </div>
        
        
    </div>
  
  
</template>
<script src="@/js/jquery-1.11.3.min.js"/>
<script>
import $ from 'jquery'
import axios from 'axios'
// import { response } from 'express'
export default {
    name:'gw',
    data(){
        return{
            info:'',
            number:1,
            gw:{
                name:'',
                price:'',
                unrl:''
            },
            content:[],
            // cricletop:'',
            // cricletop1:'',
            // cricleleft:'',
            // cricleleft1:'1100',
            show:false,
            cc:false,
            //cc是小圆圈的动画
            ccwz:'',
            
        }
    },
   
    methods:{
       
        shang(){
           
            this.number+=1;
            axios.get("http://localhost:8080/api2/things").then(response=>{
                this.info=response.data.splice(0,this.number*4)
            })
            // console.log(this.$refs)
            // this.$refs.gw.style.height=((this.number*417)+500)+'px'
            console.log(this.$refs.gw.style.height=(this.number-1)*417+500+'px',this.$refs.gw)

        },
        gm($event){
            this.gw.name=$event.target.parentElement.parentElement.parentElement.children[2].innerText.split("\n")[0]
            this.gw.price=$event.target.parentElement.parentElement.parentElement.children[2].innerText.split("\n")[1]
            this.gw.unrl=$event.target.parentElement.parentElement.parentElement.children[0].children[0].currentSrc
            // console.log($event.target.parentElement.parentElement.parentElement.children[0].children[0].currentSrc)
            // console.log($event.target.parentElement.parentElement.parentElement.children[1].children[0].src)
            // console.log($event.target.parentElement.parentElement.parentElement.children[2].innerText.split("\n")[0])


            
            // this.content.push({name:this.gw.name,price:this.gw.price,unrl:this.gw.unrl})购物车处理数据时产生问题
            
            
            // console.log($event)
            // this.cricletop=$event.target.parentElement.parentElement.parentElement.offsetTop+326
            // this.cricletop1=-$event.clientY+$event.target.parentElement.parentElement.parentElement.offsetTop+326+150
            // this.cricleleft=$event.target.parentElement.parentElement.parentElement.offsetLeft+203
            if(this.cc){
                // this.$bus.$emit('gw',this.gw)
                this.$bus.$emit('gw',JSON.parse(JSON.stringify(this.gw)))
                
            console.log(this.content)
                document.getElementById('cricle').style.display="block"
                $("#cricle").css({top:$event.target.parentElement.parentElement.parentElement.offsetTop+326,
                left:$event.target.parentElement.parentElement.parentElement.offsetLeft+203
            })
            console.log(-$event.clientY)
            $("#cricle").animate({top:-$event.clientY+$event.target.parentElement.parentElement.parentElement.offsetTop+326+150,left:this.ccwz+'px'},100,"linear")
            this.show=!this.show
            

            setTimeout(() => {
            this.show=false

            }, 300)
            }
            
        },
        gettop(el){
            if(el.offsetParent){
                // this.a=this.gettop(el.offsetParent)
                // console.log(this.a,el)
                // return this.a
                return this.gettop(el.offsetParent)
            }
            return el.offsetTop
        }

    },
    
    mounted(){
        this.$nextTick(function () {
        this.shang()
        this.$bus.$on("dh", (form) => {
                console.log("拿到用户登入的判断",form)
                !!form?this.cc=true:console.log("false")       
        });
        setTimeout(() => {
            
             this.$bus.$on("wzok", (wz) => {

                    console.log("圆球位置",wz)
                    console.log(document.body.clientWidth+"屏幕宽度")
                    console.log(this.ccwz=document.body.clientWidth-wz-this.$el.firstChild.offsetLeft-(wz/2))
                    

                    
                        
            });
        }, 0);
        if(sessionStorage.getItem("user")){this.cc=true}
       
    })
        
        
    },
    
}
</script>

<style lang="less" scoped>
.gw-0{
    display: flex;
    justify-content: center;
    .gw-1{
        position: relative;
        width: 1032px;
        // height: 500px;
        background:white;
        .gw-2{
            display: inline-block;
            margin: 10px;
            vertical-align: bottom;
            box-sizing: border-box;
            cursor: pointer;
            outline: 1px solid #e0e0e0;
            border: 1px solid rgb(199, 196, 196);
            width: 238px;
            height: 397px;
            background: white;
            position: relative;
            .gw-3shang{
                height:219px;
                background: rgb(255, 255, 255);
                
            }
            .gw-3zhong{
                position: absolute;
                top:50%;
                left:50%;
                transform:translateX(-50%);
                border:1px solid rgb(189, 166, 166);
                background:white;
                padding:7px 12px;
            }
            .gw-3xia{
                display: flex;
                justify-content: center;
                width:238px;
                height:178px;
                text-align: center;
                // background: red;
                button:hover{
                animation-play-state:running;
            }

            }
        }
        #cricle{
            border-radius: 50%;
            text-align: center;
            border: 1px solid #cb72c7;
            width: 30px;
            height: 30px;
            position: absolute;
            // display: none;
            background: #ff79af;
            line-height: 30px;
            z-index: 10;
            animation: gif 100s linear infinite;
            // animation-play-state:running;
            
        }
    }
}

</style>